<template>
      <div class="transfer">
          <template v-if="loaded">  
          <div v-if="canEditor"  >
            <div class="cell_reason">
                申请信息<span class="font_red" style="float: right">{{statusName}} </span>
            </div>
            <template  v-if="statusName == '已驳回'">
            <div class="cell_reason font_red" >
                驳回原因：{{reason}}
            </div>
            <div style="width: 100%;text-align:center;" class="cell_reason" >
                <div class="submit_btn" @click="submit('reSub')">
                重新提交申请
                </div>
            </div>
            </template>
            <div class="box_cell">
                <van-cell-group class="input_cell">
                    <van-field  label="申请人"  :disabled="true"   v-model.trim="form.name" placeholder="请输入申请人名称，必填"  :required="!canEditor" />
                    <van-field  label="联系方式" clearable :disabled="canEditor" v-model.trim="form.phone" placeholder="请输入申请人电话，必填" :error-message="errorPhone"  :required="!canEditor" />
                    <van-field  :label="transfer_type == 1? '从何转入': '转出何处'" clearable :disabled="canEditor" v-model.trim="form.remark" :placeholder="active == 0 ? '请输入从何转入(原党支部)，必填': '请输入转出何处(新党支部)，必填'"  :required="!canEditor"  />
                    <van-field v-model.trim="form.company" :disabled="canEditor" clearable  type="textarea"  rows="1" autosize label="工作单位" placeholder="请输入申请企业名称，必填"  :required="!canEditor" />
                    <van-field v-model.trim="form.address" :disabled="canEditor" clearable label="单位地址" type="textarea"  rows="2" autosize   placeholder="请输入企业地址信息，必填" :required="!canEditor" />    
                    <van-field  label="申请时间" clearable :disabled="canEditor" v-model.trim="time" placeholder=""  :required="!canEditor" />
                </van-cell-group>
            </div>
            
            <div  style="margin-top:20px;">
            <bottom />
            </div>
          </div>
          <template v-else>
          <div class="box_row clear">
              <div class="row_left"  @click="pic1 = true">
                  <img src="../../../assets/images/party/1.png" />
              </div>
              <div class="row_right"  @click="pic2 = true">
                  <img src="../../../assets/images/party/2.png" />
              </div>
          </div>
          <div class="open_pic" v-if="pic1"  @click="pic1 = false">
                  <img src="../../../assets/images/party/1.png" />
          </div>
          <div class="open_pic" v-if="pic2"  @click="pic2 = false">
                  <img src="../../../assets/images/party/2.png" />
          </div>
           <div class="box_cell" :class="{pic_show: pic1||pic2}">
            <van-tabs v-model="active">
                <van-tab title="转入"></van-tab>
                <van-tab title="转出"></van-tab> 
            </van-tabs>
             <van-cell-group class="input_cell">
                <van-field  label="申请人" :disabled="form.name"  v-model.trim="form.name" placeholder="请输入申请人名称，必填"  :required="!canEditor" />
                <van-field  label="联系方式" clearable :disabled="canEditor" v-model.trim="form.phone" placeholder="请输入申请人电话，必填" :error-message="errorPhone"  :required="!canEditor" />
                <van-field  :label="active == 0? '从何转入': '转出何处'" clearable :disabled="canEditor" v-model.trim="form.remark" :placeholder="active == 0 ? '请输入从何转入(原党支部)，必填': '请输入转出何处(新党支部)，必填'"  :required="!canEditor"  />
                <van-field v-model.trim="form.company"  :disabled="true"  v-if="columns.length>1 && !canEditor"    type="textarea"  rows="1" autosize label="工作单位" placeholder="请输入申请企业名称，必填"  :required="!canEditor" >
                <div slot="button"  @click="showCompany">
                    <div class="triangle">
                        <div class="t_content"></div>
                    </div>
                </div>
            </van-field>
              <van-field v-model.trim="form.company"  v-if="columns.length<=1 || canEditor"  label="工作单位"  :required="!canEditor" type="textarea" clearable rows="1" autosize  :disabled="columns.length == 1 || canEditor">
              </van-field>
                <van-field v-model.trim="form.address" :disabled="canEditor" clearable label="单位地址" type="textarea"  rows="2" autosize   placeholder="请输入企业地址信息，必填" :required="!canEditor" /> 
                </van-cell-group>
            </div>
        <div style="width: 100%;text-align:center;">
            <div class="submit_btn" @click="submit">
            提交申请
            </div>
        </div>
            <bottom />
          <company v-on:updateCompany="doUpdateCompany" :show="companyShow" :columns="columns"></company>
    </template>
    </template>
    <template v-else>
        <div style=" width: 100%;padding-top: 20px; font-size: 14px; color: #ccc; text-align: center;">
            加载中
        </div>
    </template>
  </div>
</template>

<script>
  import Bottom from "../common/bottom.vue";
  import Company from "../common/company.vue";
  import {isPhone} from "@/common/valid.js" 
  export default {
    name: 'transfer',
    data() {
      return {
         active: 0,
         pic1: false,
         pic2: false,
         form: {
            id: '',
            name: '',
            phone: '',
            company: '',
            address: '',
            remark: '', 
          },
          errorPhone: '',
          canEditor: false,
          statusName: '',
          time: '',
          reason: '',
          transfer_type: 1,
          companyShow: false,
          columns: [],
          loaded: false

      }
    }, 
    components: {Bottom, Company},
    watch:  {
        active() {
             this.companyShow = false;
        },
        "form.phone": function() {
            if (isPhone(this.form.phone)) { 
                this.errorPhone = '';
            }else {
                this.errorPhone = '请输入正确手机号';
            }
        }
    },
    created() {
        this.getPartyData();
    },
    methods: {
        doUpdateCompany(data) {
            if(data) {
                this.form.company = data;
            }
            this.companyShow = false;

        },
        showCompany() {
            this.companyShow = true;
        },
        getData() {  // 获取用户信息
            var data = JSON.parse(sessionStorage.users)
            this.form.name = data.name;
            this.form.phone = data.phone;
            this.form.company = data.company[0] || '';
            this.columns =  JSON.parse(JSON.stringify(data.company));

        },
        getPartyData() {  // 获取用户填写的党支信息
            this.axios.get('/app/party/detail?type=2').then(res => {
                if (res.s == 1) {  
                     this.loaded = true;
                    if(res.d != '') {
                        this.canEditor = true;
                        this.form.id = res.d.id;
                        this.form.name = res.d.name;
                        this.form.phone = res.d.phone;
                        this.form.company = res.d.company;
                        this.form.address = res.d.address;
                        this.form.remark= res.d.remark;
                        this.time = res.d.create_time;
                        this.statusName = res.d.status == 0 ? '待审核': res.d.status == 1 ? '已通过': '已驳回';
                        this.reason = res.d.operation_remark;
                        this.transfer_type = res.d.transfer_type;  // 1转入  2转出
                        var data = JSON.parse(sessionStorage.users)
                        this.columns =  JSON.parse(JSON.stringify(data.company));
                    }else {    // res.d  为空时  获取原始用户数据
                        this.getData();
                    }
                }  
            }) 
        },
        submit(type) {
            if(type == 'reSub') {
                this.canEditor = false;
                return;
            }
            var form = this.form;

            if(this.active == 0) {
                this.form.transfer_type = 1;
            }

            if(this.active == 1) {
                this.form.transfer_type = 2;
            }

            if(form.name == '' || form.phone == '' || form.company == '' || form.address == '' || form.remark == ''){
                this.$toast('请输入完整信息');
                return;
            }
            if(this.errorPhone != '') {
                return;
            }

            this.axios.post('/app/party/transfer',form).then(res => {
            if (res.s == 1) {  
                 this.$toast('提交成功!'); 
                 this.getPartyData();
                   return;
            } 
          }) 

        }
    }
  }
</script>

<style lang="scss" scoped>
@function rem($px) {
    @return $px / 50px * 1rem;
}
  .transfer {
        padding-top: rem(12px);
      .box_row {
          width: 100%;
          display:flex; 
          background: #fff;
      }
      .row_left {
          flex: 1;
          img {
            width: 100%;
          }
      }
      .row_right {
          flex: 1;
          img {
            width: 100%;
          }
      }
      .open_pic {
          position: absolute;
          top: 0;

      }
      .box_cell {
          padding-top: rem(14px);
          .van-tab  {
              font-size: rem(16px);
          }
      }

      .input_cell {
            .van-cell:not(:last-child)::after {
                left: 0;
            }
            .van-cell {
                padding: 18px 15px;
                font-size: rem(16px)
            }
      }
      .submit_btn {
            display: inline-block;
            font-size: rem(18px);
            margin: rem(20px) auto; 
            border: none;
            width: 80%;
            height: rem(48px);
            line-height: rem(48px);
            outline-style: none;
            text-align: center;
            border-style: none;
            border-radius: 5px;
            background-color: #F24B3B;
            color: #FFFFFF;

      }
      .pic_show {
          margin-top: rem(266px);

      }
       .cell_reason {
        padding: 18px 15px;
        font-size: 0.32rem; 
        background: #fff;
        position: relative;
        &:nth-child(1) {
            border-top: rem(1px) solid #eee;
        }
        &:nth-child(3) {
            &::after {
                content: ' ';
                border-bottom: 0;
            }
        }
        &::after {
            content: ' ';
            position: absolute;
            pointer-events: none;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            right: 0;
            left: 0;
            bottom: 0;
            -webkit-transform: scaleY(.5);
            transform: scaleY(.5);
            border-bottom: 1px solid #eee;
        }
    }
    button, input, select, textarea {
        line-height: 24px;

    }

  }
  
</style>

